@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;900&display=swap');
@import url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.13.0/css/all.min.css');


// RESET

html,
body {
  width: 100%;
  height: 100%;
  font-family: 'Poppins';
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

* {
  font-family: inherit;
  box-sizing: inherit;
}

#root {
  width: 100%;
  height: 100%;
}

.wrapper {
  position: relative;
  overflow: hidden;
  height: 100%;
  width: 100%;

  background-color: #cfcfd6;

  //navbar 
  nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    width: 100%;
    padding: 10px 30px;

    i {
      font-size: 18px;
      margin: 0 10px;
    }

    .lang {
      margin-left: auto;
      font-size: 12px;
      font-weight: 600;
      text-transform: uppercase;
    }




  }



  // social

  ul.social {
    position: absolute;
    bottom: 230px;
    left: -150px;
    transform: rotateZ(-90deg);
    margin: 0;
    padding: 10px;
    list-style: none;

    li {
      display: inline;
      margin-right: 80px;
      font-size: 10px;
      font-weight: 600;
      text-transform: lowercase;
    }
  }


  ul.q-nav {
    position: absolute;
    top: 100px;
    right: -50px;
    margin: 0;
    padding: 10px;
    list-style: none;

    li {
      &:hover {
        text-decoration: underline;
      }

      text-align: right;
      margin-right: 80px;
      font-size: 10px;
      font-weight: 600;
      text-transform: lowercase;
    }
  }

  .text-yellow {
    color: #d3ba4a;
  }

  .overlay {
    background: #d3ba4a;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 100;

    &.st {
      left: 0;
    }

    &.nd {
      left: 33.3%;
    }

    &.rd {
      left: 66.6%;
    }
  }


  // sponsor
  .sponsor {
    font-size: 12px;
    text-align: center;
    position: absolute;
    bottom: 100px;
    right: 100px;

    p {
      margin: 0;
      padding: 0;
    }

    h4 {
      margin: 0;
      padding: 0;
      font-size: 20px;
      letter-spacing: -1px;
      text-transform: uppercase;
      font-weight: 900;
    }
  }


  // text
  .text {
    position: absolute;
    transform: rotate(-90deg);

    left: 100px;
    top: 200px;
    overflow: visible;

    h1,
    h2,
    h3,
    p {
      margin: 0;
      padding: 0;
      line-height: 1.25
    }

    h1 {
      font-size: 125px;
      letter-spacing: -10px;
      width: 650px;
      height: 150px;
      font-weight: 900;
      text-transform: uppercase;
      margin-bottom: 15px;
      line-height: 1.2;
      white-space: nowrap;
      position: relative;
      overflow: hidden;

    }

    h2 {
      overflow: visible;
      white-space: nowrap;
      position: absolute;
      font-size: 130px;
      font-weight: 700;
      letter-spacing: 1px;
      z-index: -1;
      top: 44px;
      left: -70px;
      color: #d3ba4a;
      text-transform: uppercase;
    }

    h3 {
      font-size: 34px;
      text-transform: uppercase;
      font-weight: 500;
      line-height: 1.2;
      width: 600px;
    }

    p {
      width: 200px;
      font-size: 12px;
      margin-top: 30px;
      font-weight: 500;
      line-height: 1.5;
      position: relative;
      overflow: hidden;
      height: 110px;
    }
  }


  // img
  #logo {
    position: absolute;
    top: 50%;
    right: -10%;
    transform: translate(-50%, -50%);
    width: 90vh;
    height: 90vh;

    g {
      g:first-child {
        path:nth-child(1) {
          animation: morph 2.4s infinite ease;
        }
      }

      g:nth-child(3) {
        path:nth-child(1) {
          animation: stroked 12s infinite ease;
        }
      }

      g:nth-child(5) {
        path:nth-child(2) {
          animation: morph 10s infinite ease;
        }
      }



      g:nth-child(2) {
        path {
          animation: morph2 2s infinite ease;
        }
      }

      g:nth-child(4) {
        path:nth-child(1) {
          animation: morph2 2s infinite ease;
        }
      }

    }
  }

}


@keyframes stroked {
  0% {
    stroke-dasharray: 280;
  }

  100% {
    stroke-dasharray: 280;
  }

  50% {
    stroke-dasharray: 100;
  }

}

@keyframes morph {
  0% {
    fill: #cfcfd6
  }

  50% {
    fill: #d3ba4a
  }

  100% {
    fill: #cfcfd6
  }
}

@keyframes morph2 {
  0% {
    fill: rgba(0, 0, 0, 0.75);
  }

  50% {
    fill: #d3ba4a
  }

  100% {
    fill: rgba(0, 0, 0, 0.75);
  }
}